.jvectormap-label {
  position: absolute;
  display: none;
  border: solid 1px #cdcdcd;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  background: #292929;
  color: white;
  font-family: sans-serif, Verdana;
  font-size: smaller;
  padding: 3px;
}

.jvectormap-zoomin,
.jvectormap-zoomout {
  position: absolute;
  left: 10px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  background: #424242;
  padding: 2px;
  color: white;
  width: 15px;
  height: 15px;
  cursor: pointer;
  line-height: 10px;
  text-align: center;
}

.jvectormap-zoomin {
  top: 10px;
}

.jvectormap-zoomout {
  top: 30px;
}
